body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  background: #030a20; }

.container-l {
  width: 90%;
  margin: 0 auto; }

.container-m {
  width: 80%;
  margin: 0 auto; }

.btn-blue {
  background: #36598d;
  color: white;
  padding: 5px;
  height: 40px;
  width: 40px;
  border-radius: 5px;
  font-size: 24px;
  border: none; }

.btn-pink {
  width: 100%;
  color: white;
  font-size: 31px;
  border: solid 2px #f11176;
  padding: 15px;
  border-radius: 10px;
  font-weight: 700;
  background-color: #f11176;
  background-image: linear-gradient(#ff71b2, #f11176);
  box-shadow: 0px 1px 4px -2px #333; }

.product__link {
  color: #f11176;
  cursor: pointer; }

.sectionTitle {
  font-size: 25px;
  padding: 0;
  color: white;
  padding-bottom: 15px; }

.container-desktop {
  width: 100%;
  margin: 0 auto; }

.productDesktop__header {
  display: grid;
  grid-template-columns: 1fr; }

.productDesktop__content {
  display: grid;
  grid-template-columns: 1fr; }

@media (min-width: 800px) {
  .container-desktop {
    width: 80%;
    margin: 0 auto; }
  .productDesktop__header {
    display: grid;
    grid-template-columns: 1fr 1fr; }
  .productDesktop__content {
    display: grid;
    grid-template-columns: 1fr 1fr; } }

nav {
  background: #030a20;
  color: white; }

.navbar__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 30px 0; }

.hamburger-menu {
  font-size: 22px;
  cursor: pointer; }

footer {
  background: #00183c;
  color: #758aa9;
  font-size: 12px;
  padding: 20px 0; }
  footer p {
    margin: 0; }

.productImage {
  background: #030a20;
  margin: 0 auto; }
  .productImage img {
    max-width: 100%; }

.productHeader {
  background: #00183c;
  padding: 10px 0; }
  .productHeader h1 {
    font-size: 32px;
    color: white;
    margin: 0; }
  .productHeader p {
    font-size: 18px;
    color: #36598d;
    margin: 0; }

.productCart {
  background: #030a20;
  color: white;
  padding: 20px 0 40px 0; }

.productCart__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-bottom: 20px; }

.productCart__price {
  padding-top: 12px;
  vertical-align: text-top;
  font-weight: 700;
  align-self: start; }

.productCart__price__pounds {
  font-size: 40px; }

.productCart__price__pence {
  font-size: 26px;
  position: absolute;
  margin-top: 5px; }

.productCart__Quantity {
  text-align: center;
  justify-self: center; }
  .productCart__Quantity button, .productCart__Quantity h3 {
    display: inline; }
  .productCart__Quantity h3 {
    color: #36598d;
    font-size: 16px;
    padding-bottom: 5px; }

.productCart__Quantity__buttons h1 {
  display: inline;
  color: white;
  padding: 0 8px;
  font-size: 32px; }

.productDescription {
  padding: 20px 0;
  background: #00183c;
  color: white; }

.productSpecifications {
  background: #030a20;
  color: white;
  font-weight: 400;
  padding: 20px 0; }

.productSpeicifcations__row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-bottom: 0.5px solid #00183c;
  padding: 8px 0;
  font-size: 16px; }

.productPerformance {
  background: #030a20;
  color: white;
  padding: 20px 0 50px 0; }
  .productPerformance img {
    width: 100%; }



